import React from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { Row, Col, Input } from 'reactstrap'
import { addError, removeError, changeValue } from '../../modules/reliefs'

const Reliefs = props => (
  <div className='pt-2'>
    {props.info.map(item =>
      <Row key={item.id} style={{ 'display': 'flex', 'flexDirection': 'row', 'width': '100%' }} noGutters>
        <Col xs='1'>
          <p className='p-1'>{item.id}</p>
        </Col>
        <Col xs='6' sm='7'>
          <p className='p-1'>{item.description[0]}</p>
        </Col>
        <Col xs='2'>
          <p className='p-1 text-left'>{item.limit}</p>
        </Col>
        <Col xs='3' sm='2'>
          <p className='p-1'>
            <Input placeholder='Enter Amount'
              style={{ 'textOverflow': 'ellipsis' }}
              type='text' pattern='\d*'
              valid={props.errorIds.indexOf(item.id) > -1 ? false : item.value ? true : null}
              value={item.value}
              onChange={
                event => {
                  var newVal = event.target.value
                  if (!/^[\d]*[\\.]?[\d]*$/.test(newVal)) {
                    newVal = ''
                  }
                  props.changeValue(item.id, newVal)

                  if (newVal < 0 || newVal > item.limit) {
                    props.addError(item.id)
                  } else {
                    props.removeError(item.id)
                  }
                }
              } />
          </p>
        </Col>
      </Row>)
    }
  </div>
)




const mapStateToProps = state => ({
  info: state.reliefs.info,
  errorIds: state.reliefs.errorIds,
  eligibleRelief: state.reliefs.eligibleRelief
})


const mapDispatchToProps = dispatch => bindActionCreators({
  addError,
  removeError,
  changeValue
}, dispatch)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Reliefs)



