export const NAVIGATE_NEXT = 'calculator/NAVIGATE_NEXT'
export const NAVIGATE_BACK = 'calculator/NAVIGATE_BACK'



const initialState = {
  view: 'reliefs',
}


export default (state = initialState, action) => {
  switch (action.type) {
    case NAVIGATE_NEXT:
      if (state.view === 'reliefs') {
        return {
          ...state,
          view: 'result'
        }
      }
      return state

    case NAVIGATE_BACK:
      if (state.view === 'result') {
        return {
          ...state,
          view: 'reliefs'
        }
      }
      return state

    default:
      return state
  }
}

export const navigateNext = () => {
  return dispatch => {
    dispatch({
      type: NAVIGATE_NEXT
    })
  }
}

export const navigateBack = () => {
  return dispatch => {
    dispatch({
      type: NAVIGATE_BACK
    })
  }
}



