import React from 'react'
import { Row, Col, Input } from 'reactstrap'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { changeGrossIncome, changeNonTaxable } from '../../modules/tax-info'



const TaxInfo = props => (
  <div style={{ 'display': 'flex', 'flexDirection': 'column' }}>
    <div className='m-2'></div>
    <div className='my-2'>
      <Row className='justify-content-md-center'>
        <Col className='col-lg-4'><h4>Eligible Tax Relief</h4></Col>
        <Col className='col-lg-2'>
          <div
            className='text-center bg-light form-control font-weight-bold'>
            {props.eligibleRelief}
          </div>
        </Col>
      </Row>
    </div>
    <div className='my-2'>
      <Row className='justify-content-md-center'>
        <Col className='col-lg-4'><h4>Gross Income</h4></Col>
        <Col className='col-lg-2'>
          <Input placeholder='Enter Amt' type='text' pattern='\d*'
            value={props.grossIncome}
            onChange={
              event => {
                var newVal = event.target.value
                if (!/^[\d]*[\\.]?[\d]*$/.test(newVal)) {
                  newVal = ''
                }
                props.changeGrossIncome(newVal)
              }
            } />
        </Col>
      </Row>
    </div>
    <div className='my-2'>
      <Row className='justify-content-md-center'>
        <Col className='col-lg-4'><h4>Non-taxable allowance</h4></Col>
        <Col className='col-lg-2'><Input placeholder='Enter Amt'
          value={props.nonTaxable}
          onChange={
            event => {
              var newVal = event.target.value
              if (!/^[\d]*[\\.]?[\d]*$/.test(newVal)) {
                newVal = ''
              }
              props.changeNonTaxable(newVal)
            }
          } /></Col>
      </Row>
    </div>
    <div className='my-2'>
      <Row className='justify-content-md-center'>
        <Col className='col-lg-4'>
          <span className='h4'>Taxable Income</span>
          <span className='h4 float-right font-weight-bold'>=</span>
        </Col>
        <Col className='col-lg-2'>
          <div
            className='text-center bg-success text-white form-control font-weight-bold'>
            {props.grossIncome - props.nonTaxable - props.eligibleRelief > 0
              ? props.grossIncome - props.nonTaxable - props.eligibleRelief
              : '-'}
          </div>
        </Col>
      </Row>
    </div>
  </div>
)




const mapStateToProps = state => ({
  eligibleRelief: state.reliefs.eligibleRelief,
  grossIncome: state.taxInfo.grossIncome,
  nonTaxable: state.taxInfo.nonTaxable
})



const mapDispatchToProps = dispatch => bindActionCreators({
  changeGrossIncome,
  changeNonTaxable
}, dispatch)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TaxInfo)



