export const CHANGE_GROSS_INCOME = 'tax/CHANGE_GROSS_INCOME'
export const CHANGE_NON_TAXABLE = 'tax/CHANGE_NON_TAXABLE'


const initialState = {
  eligibleRelief: 0,
  grossIncome: '',
  nonTaxable: ''
}

export default (state = initialState, action) => {
  switch (action.type) {
    case CHANGE_GROSS_INCOME:
      return {
        ...state,
        grossIncome: action.value
      }
    case CHANGE_NON_TAXABLE:
      return {
        ...state,
        nonTaxable: action.value
      }

    default:
      return state
  }
}

export const changeGrossIncome = value => {
  return dispatch => {
    dispatch({
      type: CHANGE_GROSS_INCOME,
      value: value
    })
  }
}

export const changeNonTaxable = value => {
  return dispatch => {
    dispatch({
      type: CHANGE_NON_TAXABLE,
      value: value
    })
  }
}

export const changeTaxable = value => {
  return dispatch => {
    dispatch({
      type: CHANGE_NON_TAXABLE,
      value: value
    })
  }
}



