import React from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink
} from 'reactstrap';
import { NavLink as RRNavLink, withRouter } from 'react-router-dom'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'

const AppNavBar = props => (
  <div>
    <div style={{ 'height': '70px' }}></div>

    <div className='fixed-top' >
      <Navbar color='light' light expand='md' style={{ 'backgroundColor': '#F8F8F8' }}>
        <NavbarBrand href='/'><h4>Tax Refund Calculator 2017</h4></NavbarBrand>
        <NavbarToggler onClick={props.toggleNav} />
        <Collapse isOpen={props.isNavOpen} navbar>
          <Nav className='ml-auto' pills>
            <NavItem>
              <NavLink activeClassName='bg-info text-white font-weight-bold' exact tag={RRNavLink} to='/'>Calculator</NavLink>
            </NavItem>
            <NavItem>
              <NavLink activeClassName='bg-info text-white font-weight-bold' exact tag={RRNavLink} to='/info'>Tax Info</NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
    </div>
  </div>

)

const mapStateToProps = state => ({
})

const mapDispatchToProps = dispatch => bindActionCreators({
}, dispatch)

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(AppNavBar))



