import React from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { Button } from 'reactstrap'
import Reliefs from '../reliefs'
import FloatingAlert from '../floating-alert'
import ReliefsHeader from '../reliefs-header'
import { navigateNext, navigateBack } from '../../modules/calculator'
import TaxInfo from '../tax-info'

const Home = props =>
  (
    <div className='bg-white m-1 mt-3'>
      <FloatingAlert />
      <div style={{ 'display': 'flex', 'flexDirection': 'column' }}>
        {
          props.view === 'reliefs' &&
          <div>
            <ReliefsHeader />
            <Reliefs />
          </div>
        }
        {
          props.view === 'result' &&
          <div>
            <TaxInfo />
          </div>
        }
        <div className='my-4'></div>
        {
          props.view === 'reliefs' &&
          <Button color='primary' style={{
            'position' : 'fixed',
            'width' : '110px',
            'height' : '35px',
            'margin': '10px auto',
            'left': '0',
            'right' : '0',
            'bottom' : '0'
          }} onClick={props.navigateNext}>Next</Button>
        }
        {
          props.view === 'result' &&
          <Button color='primary' style={{
            'position' : 'fixed',
            'width' : '110px',
            'height' : '35px',
            'margin': '10px auto',
            'left': '0',
            'right' : '0',
            'bottom' : '0'
          }} onClick={props.navigateBack}>Back</Button>
        }
      </div>
    </div>

  )





const mapStateToProps = state => ({
  view: state.calculator.view
})


const mapDispatchToProps = dispatch => bindActionCreators({
  navigateNext,
  navigateBack
}, dispatch)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Home)



